@font-face { font-family: 'NanumBarunpen Bold'; src: url(./fonts/NanumBarunpenB.ttf) format('truetype'); }

.Home {
    align-items: center;
    text-align: center;
}

.MainImage {
    align-items: center;
    text-align: center;

    max-width: 640px;

    position: relative;

    margin: 0 auto; /* 페이지 중앙에 나타나도록 설정 */

    display: flex;
    flex-direction: column;
}

.StartButton {
    margin: -600px 0px;

    color: white;
    background: #2e425c;
    padding: 24.3px 59.4px;
    font-size: 35px;
    font-family: 'NanumBarunpen Bold';
    line-height: 1.7;

    border: 6.3px solid #95c8e6;
    border-radius: 47.7px;
    box-shadow: 0 0 11.7px 0 #008cff;

    width: 389.7px;
}